body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@-webkit-keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}

	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}

	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

body>#root>div {
	height: 100vh;
}

body {
	background-color: rgb(24, 168, 120);
}

ul>li {
	min-width: 140px;
}

  


.rotating {
	width: 11%;
	height: 11%;
    -webkit-animation: rotating 20s linear infinite;
            animation: rotating 20s linear infinite;
}
@-webkit-keyframes rotating {
    from {-webkit-transform:rotate(0deg);transform:rotate(0deg);}
    to {-webkit-transform:rotate(360deg);transform:rotate(360deg);}
}
@keyframes rotating {
    from {-webkit-transform:rotate(0deg);transform:rotate(0deg);}
    to {-webkit-transform:rotate(360deg);transform:rotate(360deg);}
}
.imgContainer {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-around;
	        justify-content: space-around;
}

.blackBtn{
	width: 50px;
	height: 50px;
	margin: 5px;
	border-radius: 10%;
	border: none;
	background-color: #6a6d6b;
	font-size: 0.7rem;
}
.blackBtn:hover, .blackBtn:active{
	transition: background-color 0.5s ease;
	background-color: #4f5150;
	border: none!important;
	border-radius: 10%;
}
.blackBtn:focus{
	outline: none;
}
@media (max-width: 576px) {
    .memoryGif {
        min-height: 240px;
        min-width: 240px;
    }
    .theMug {
        min-height: 240px;
        min-width: 240px;
    }
  }
  

.memoryGif {
    max-height: 480px;
    max-width: 480px;
    margin: 20px;
}


.projectwrapper{
    width: 100vw;
}
.theMug{
    max-height: 480px;
    max-width: 480px;
    padding: 90px;
    background-color: rgba(234, 206, 93, 0.9);
    border-radius: 10%;
    margin: 20px;
}
.headlinebackground{
    background-color: rgb(226, 89, 89);
    padding: 2px;
    margin-top: 30px;
    margin-bottom: 50px;
    font-size: 1.5rem;
    font-weight: 500;
}

a .carousel-control-next-icon, a .carousel-control-prev-icon {
	background-color: rgba(66, 83, 109, 0.5) !important;
	width: 40px;
	height: 40px;
	border-radius: 25%;
}

.imageClass {
	/* border: 1px solid green; */
	box-shadow: 10px 5px 5px grey;
}

.carousel-indicators li {
	display: none !important;
}

.carousel-inner > img{
	height: 250px;
    margin:auto;
}
